___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

p {
    margin: 0;
}

.app {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: flex;

}

ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

#todo li {
    padding: 0;
    margin: 0;
}

html {
    width: 100%;
    height: auto;
}

body {
    width: 100%;
    height: 100%;
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(219,153,242,1) 0%, rgba(255,209,209,1) 100%);
    display: flex;
    overflow:hidden;
}

@media (max-width: 700px) {
    body {

    }
}

.app {
    width: 100%;
    height: 100%;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    overflow: auto;
    gap: 40px;
}

#todo {
    max-width: 80%;
    max-height: 80%;
    -webkit-box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
    margin: auto;
    display: flex;
    justify-content: space-around;
    gap: 100px;
    border-radius: 10px;
    padding: 20px;
}

#todo ul li {
    display: flex;
    gap: 20px;
    align-items: center;
    justify-content: center;

}

#checklist li p {
    font-size: 1.5rem;
    text-transform: uppercase;
    font-family:'Courier New', Courier, monospace;
    font-weight: bold;
}

#notes li p {
    font-size: 1rem;
    text-transform: uppercase;
    font-family:'Courier New', Courier, monospace;
    font-weight: bold;
}

#noteslist {
    max-width: 60%;
    max-height: 100%;
}

#checklist {
    max-width: 40%;
    max-height: 100%;
    overflow: auto;
}

.check-button {
    border-radius: 50%;
    border-style: none;
    background-color: #fff;
    min-width: 30px;
    height: 30px;
}

#todo li p {
    max-width: auto;
    height: auto;
}

#todo input {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    border: none;
    padding: 10px;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-family:'Courier New', Courier, monospace;
    font-weight: bold;
}

#todo input {
    display: none;
}

#todo p {
    word-break: break-all;
}

#todo .plus-button {
    display: none;
    border-radius: 10px;
    border-style: none;
    background-color: #fff;
    width: 60px;
    height: 45px;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-family:'Courier New', Courier, monospace;
    font-weight: bold;
}

.note-button {
    border-radius: 5px;
    border-style: none;
    background-color: #fff;
    min-width: 30px;
    height: 30px;
    font-weight: bold;
    font-size: 15px;
    align-self: flex-start;
}

button:hover {
    opacity: 90%;
}

button:active {
    opacity: 80%;
}

#checklist {
    height: fit-content;
    border-radius: 10px;
    padding: 20px;
    background: rgb(212,207,29);
    background: linear-gradient(90deg, rgba(212,207,29,1) 0%, rgba(255,240,140,1) 100%); 
}

#checklist ul {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

#checklist li {
    border-radius: 10px;
    max-width: 100%;
    height: auto;
    padding: 10px;
    -webkit-box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
}


.add-check-button {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 10px;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-family:'Courier New', Courier, monospace;
    font-weight: bold;
}

.add-notes-button {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 10px;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-family:'Courier New', Courier, monospace;
    font-weight: bold;
}

.check-button {
    margin-right: auto;
}

#checklist ul li div {
    display: none;
}

#todo li p {
    margin-right: auto;
}

#noteslist ul {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 20px;
    -webkit-box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
    border-radius: 10px;
    overflow: auto;
}

#noteslist li {
    padding: 10px;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 0px 57px 0px rgba(34, 60, 80, 0.2);
}

/* калькулятор */

#calculatorblock {
    max-width: auto;
    height: auto;
    margin: auto;
    padding: 20px;
    border-radius: 10px;
    background: linear-gradient(90deg, rgba(151, 147, 233, 0.253) 0%, rgba(178, 108, 204, 0.45) 35%, rgba(0, 213, 255, 0.144) 400%);
    overflow: auto; 
}

#calculator #zero {
    width: 100%;
}

#calculator button {
    border-radius: 5px;
    border-style: none;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    font-size: large;
}

#calculator button:active {
    opacity: 90%;
}

#calculator .buttons {
    display: grid;
    grid-template-columns: 1fr;
    gap: 10px;
}

#calculator .buttons div button {
    width: 60px;
    height: 60px;
}
#calculator #input {
    width: 100%;
    margin-bottom: 20px;
    height: 50px;
    border-radius: 5px;
    border: none;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: medium;
    padding-left: 10px;
}

#calculator .buttons div {
    width: 100%;
}

#input:active {
    border: none;
}

#input:focus {
    border-style: none;
}

#result {
    height: 60px;
}

#bigplus {
    width: 100%;
    height: 60px;
    margin-left: auto;
    margin-right: auto;
}

@keyframes musicbarup {
    from {
        bottom: -50px;
        opacity: 50%;
    } 
    to {
        bottom: 0;
        opacity: 100%;
    }
}

@keyframes musicbardown {
    from {
        bottom: 0;
        opacity: 100%;
    } 
    to {
        bottom: -50px;
        opacity: 50%;
    }
}

#audioplayer {
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    margin-top: auto;
    bottom: -50px;
    border-radius: 10px 10px 0px 0px;
    width: 60%;
    height: auto;
    opacity: 50%;
    background-color: rebeccapurple;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.audioplayer__controls img {
    max-width: 54px;
    height: auto;
}

#audioplayer-main-image {
    max-width: 54px;
    height: auto;
    border-radius: 5px;
}

#audioplayer__artist-name {
    font-weight: bold;
}

#audioplayer__artist-name , #audioplayer__song-name {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: larger;
}

#pause-control {
    display: none;
    max-width: 54px;
    height: auto;
}



